<template>
  <article class="content" basics-section>
    <div basics-prose>
      <h1 basics-heading basics-text>About me</h1>
      <p basics-text>
        Hi, I'm Dmytro.
      </p>
      <p>A {{age}}-year-old software engineer from Ukraine.</p>
    </div>
    <div basics-prose>
      <p basics-text>
        Started coding in 2018 and became a full-time developer in Q3 2021 as a full-stack developer, with a bit more focus on frontend development. I’m good at technical aspects but bad at creating stunning visuals.
      </p>
      <p basics-text>
        I love working on unusual projects, especially those involving real-world devices, so I want to focus on IoT or embedded development.
      </p>
    </div>

    <div basics-prose>
      <p>Languages:</p>
      <div basic-list>
        <p>
          Ukrainian (native)
        </p>
        <p>
          English (B2)
        </p>
      </div>
    </div>

    <div basics-prose>
      <p>Experience:</p>
      <div basic-list>
        <CompanyExperience v-if="experience.qs.output"
                           companyName="Qualium Systems"
                           :data="experience.qs.output"
                           :websiteLink="links.qualiumSystems"
                           :linkedinLink="links.qualiumSystems_linkedIn"
                           workType="Remote" />
        <CompanyExperience v-if="experience.nexat.output"
                           companyName="Nexat"
                           :data="experience.nexat.output"
                           :websiteLink="links.nexat"
                           :linkedinLink="links.nexat_linkedIn"
                           workType="Remote / Office / Field" />
      </div>
    </div>

  </article>
</template>

<script lang="ts">
  import { defineComponent } from 'vue';
  import { CompanyExperience } from "@/components/company-experience"
  import { Links } from '../utils/LinksUtils';

  interface CompanyExperienceInput {
    start: string,  //yyyy-mm-dd
    end: string | null,   //yyyy-mm-dd
  }

  export interface CompanyExperienceData {
    formattedStart: string,
    formattedEnd: string,
    yrs: number,
    mos: number
  }

  export default defineComponent({
    name: 'AboutView',
    components: {
      CompanyExperience
    },
    props: {
    },
    watch: {
    },
    data() {
      return {
        birthDate: '1998-04-08',  //yyyy-mm-dd
        age: 26,
        links: Links,
        experience: {
          qs: {
            input: {
              start: '2022-09',
              end: null,
            } as CompanyExperienceInput,
            output: {
              formattedStart: '-',
              formattedEnd: '-',
              yrs: 0,
              mos: 0
            } as CompanyExperienceData
          },
          nexat:
          {
            input: {
              start: '2021-05',
              end: '2022-08',
            } as CompanyExperienceInput,
            output: {
              formattedStart: '-',
              formattedEnd: '-',
              yrs: 0,
              mos: 0
            } as CompanyExperienceData
          } 
        }
      };
    },
    mounted() {
      this.age = this.calculateAge();
      this.experience.qs.output = this.calculateExperience(this.experience.qs.input);
      this.experience.nexat.output = this.calculateExperience(this.experience.nexat.input);
    },
    methods: {
      calculateAge(): number {
        const today = new Date();
        const birth = new Date(this.birthDate);

        let age = today.getFullYear() - birth.getFullYear();
        const monthDiff = today.getMonth() - birth.getMonth();

        if (monthDiff < 0 || (monthDiff === 0 && today.getDate() < birth.getDate())) {
          age--;
        }

        return age < 26 ? 26 : age;
      },
      calculateExperience(company: CompanyExperienceInput): CompanyExperienceData {
        const options: Intl.DateTimeFormatOptions = { month: 'long', year: 'numeric' };

        const start = new Date(company.start);
        const formattedStart = start.toLocaleString('en-US', options);

        let formattedEnd = 'Present';
        let end = new Date();

        if (company.end) {
          end = new Date(company.end);
          formattedEnd = end.toLocaleString('en-US', options);
        } 

        let yrs = end.getFullYear() - start.getFullYear();
        let mos = end.getMonth() - start.getMonth();

        if (mos < 0 || (mos === 0 && end.getDate() < start.getDate())) {
          yrs--;
          mos += 12;
        }

        mos += 1; //Month included

        return {
          formattedStart, formattedEnd, yrs, mos
        };
      }
    }
  });
</script>

