    <a v-if="websiteLink" :href="websiteLink" target="_blank" highlight><Link height="32px" /></a>
    <a v-if="linkedinLink" :href="linkedinLink" target="_blank" highlight><LinkedIn height="32px" /></a>
  </div>
</template>

